import { getProfileAPI, getStatusAPI, updateStatusAPI } from "../api/api";

const SET_USER_DATA = "profile/SET_USER_DATA";
const SET_USER_STATUS = "profile/SET_USER_STATUS";
const ADD_POST = "profile/ADD_POST";

let initialState = {
  profile: null,
  status: "",
  posts: [],
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATA:
      return {
        ...state,
        profile: action.profile,
        status: action.status,
      };
    case SET_USER_STATUS:
      return {
        ...state,
        status: action.status,
      };
    case ADD_POST:
      let newPost = {
        id: state.posts.length,
        message: action.newPostText,
        createTime: new Date(),
      };

      return {
        ...state,
        posts: [...state.posts, newPost],
      };

    default:
      return state;
  }
};

export const setUserData = (profile, status) => {
  return { type: SET_USER_DATA, profile, status };
};
export const setUserStatus = (status) => {
  return { type: SET_USER_STATUS, status };
};
export const addPost = (newPostText) => {
  return { type: ADD_POST, newPostText };
};

export const getUserData = (userId) => async (dispatch) => {
  let results = await Promise.all([
    getProfileAPI(userId),
    getStatusAPI(userId),
  ]);

  dispatch(setUserData(results[0], results[1]));
};

export const updateUserStatus = (userStatus) => async (dispatch) => {
  let statusData = await updateStatusAPI(userStatus);
  if (statusData.resultCode === 0) {
    dispatch(setUserStatus(userStatus));
  }
};

export default profileReducer;
