import React from "react";
import { compose } from "redux";
import { connect } from "react-redux";
import { Routes, Route } from "react-router-dom";
import { initializeApp } from "./redux/app.reducer";
import LoginPage from "./components/LoginPage/LoginPage.jsx";
import HeaderContainer from "./components/Header/HeaderContainer.jsx";
import NavBarContainer from "./components/NavBar/NavBarContainer.jsx";
import Preloader from "./components/Common/Preloader/Preloader";
import "./App.css";

const ProfileContainer = React.lazy(() =>
  import("./components/Content/Profile/ProfileContainer")
);

const UsersContainer = React.lazy(() =>
  import("./components/Content/Users/UsersContainer")
);

const MessagesContainer = React.lazy(() =>
  import("./components/Content/Messages/MessagesContainer.jsx")
);

class App extends React.Component {
  componentDidMount() {
    this.props.initializeApp();
  }

  render() {
    if (this.props.initialized) {
      return (
        <div className="app-wrapper">
          <HeaderContainer />
          <NavBarContainer />
          <div className="app-wrapper-content">
            <React.Suspense fallback={<Preloader />}>
              <Routes>
                <Route path="/login" element={<LoginPage />} />
                <Route path="/profile/:userId" element={<ProfileContainer />} />
                <Route path="/messages/*" element={<MessagesContainer />} />
                <Route path="/users/*" element={<UsersContainer />} />
              </Routes>
            </React.Suspense>
          </div>
        </div>
      );
    } else {
      return <Preloader absolutePosition={true} />;
    }
  }
}

let mapStateToProps = (state) => {
  return {
    initialized: state.app.initialized,
    isAuth: state.auth.isAuth,
  };
};

export default compose(
  connect(mapStateToProps, {
    initializeApp,
  })
)(App);
